@import '../../colors';
@import '../../keyframes';
@import '../../mixins';

.about {

    &__container {
        width: 100%;
        height: 100%;
        margin: 0;
        padding: 0;
        display: flex;
        flex-flow: column nowrap;
        justify-content: center;
        align-items: center;
        animation-name: enter;
        animation-duration: 1s;
        animation-timing-function: ease;

        @include respond (phone) {
            & {
                justify-content: flex-start;
                padding-top: 10rem;
            }
        }

        & h1 {
            margin-top: 0;
        }
    }

    &__title {
        margin-bottom: 4rem;
    }

    &__profile-picture {
        height: 20rem;
        width: 20rem;
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
        background: $main-gradient;
        position: relative;
        margin-bottom: 6rem;
        box-shadow: .5rem .5rem 1rem .5rem rgba(0, 0, 0, 0.3);
        transition: all 1s;
        -webkit-transition: all 1s;
        -moz-transition: all 1s;
        -ms-transition: all 1s;
        -o-transition: all 1s;

        @include respond (phone) {
            & {
                padding: 10rem 0rem;
            }
        }


        & img {
            height: 92%;
            border-radius: 50%;
            -webkit-border-radius: 50%;
            -moz-border-radius: 50%;
            -ms-border-radius: 50%;
            -o-border-radius: 50%;
            border: 3px solid $main-gradient;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            -webkit-transform: translate(-50%, -50%);
            -moz-transform: translate(-50%, -50%);
            -ms-transform: translate(-50%, -50%);
            -o-transform: translate(-50%, -50%);
        }
    }

    &__description {
        width: 80%;
        font-size: 2rem;
        line-height: 3rem;
        text-align: justify;
        background: $app-boxes;
        color: $light-gray;
        font-weight: 400;
        border-radius: 1rem;
        -webkit-border-radius: 1rem;
        -moz-border-radius: 1rem;
        -ms-border-radius: 1rem;
        -o-border-radius: 1rem;
        padding: 1rem;
        border: 2px solid $light-gray;

        & p {
            padding: 0 3rem;
        }
    }

    &__buttons {
        margin: 0 auto;
        padding-top: 4rem;
        display: flex;
        flex-flow: row nowrap;
        width: 60%;
        align-self: center;
        justify-content: space-evenly;

        @include respond (phone) {
            & {
                margin-bottom: 3rem;
                margin: 0;
                width: 100%;
                justify-content: center;

                & button {
                    width: fit-content;
                    margin-right: 2rem;
                }
            }
        }
        
        & button {
            box-shadow: .7rem .7rem 1.4rem .8rem rgba(0, 0, 0, 0.3);
        }
    }
}